<template>
	<div>
		<div class="certificate">
			<div  class="certificate__wrapper">
				<div class="certificate__header">
					<h5>Exam Instructions</h5>	
				</div>
				<div class="certificate__body">
					<p v-html="itemData.exam_instructions"></p>
				</div>
                <div class="btn__group btn__group--form">
                    <div>
                        <label>
                            <input type="checkbox" @click="toggleCheckbox" />
                            <span class="terms">I agree to the Exam instructions</span>
                        </label>
                    </div>
                    <button
                        :disabled="!checked"
                        @click.prevent="changeAgreement"
                        class="btn__primary">Confirm</button>
                </div>
			</div>
		</div>
	</div>
</template>

<script>
import eventHub from '../../utils/eventhub';
import { success, fail } from '../../utils/toast';

export default {
	name: 'ExamInstructionModal',
	props: {
		itemData: {
			type: Object,
			default() {
				return {};
			}
		}
    },
    data() {
        return {
            checked: false
        };
    },
    methods: {
        changeAgreement() {
            if (this.checked === true) {
                success({
                    title: 'Success! You can now take the exam!'
                });
                eventHub.$emit('changed-instruction-status', this.checked);
            } else {
                fail({
                    title: 'Oops! You need to agree to the exam instructions',
                });
            }
        },
        toggleCheckbox() {
            this.checked = !this.checked;
        }
    }
};
</script>

